@import "base/palette";
@import "base/typography";

.purr-btn {
  width: 120px;
  display: flex;
  position: relative;
  flex-direction: row;
  align-items: center;
  align-content: stretch;
}

a.purr-btn.disabled {
  pointer-events: none;
  cursor: default;
}

.purr-loader {
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  padding: 3px;
  background: #ececec;
  position: absolute;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: #777;
}

.fixed-height {
  height: 100%;
  max-height: 45vh;
  overflow: auto;
}

.action-box > .section > .icon.icon-small {
  font-size: 1rem;
  padding-right: 0.5rem;
  flex-shrink: 0;
  align-self: center;
}


.purr-row {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  .purr-col {
    min-width: 150px;
    flex: 0 1 auto;

    display: flex;
    gap: 5px;
    justify-content: flex-start;
    align-items: baseline;

    .label {
      font-weight: bold;
    }
  }

  .purr-col-grow {
    flex: 1 1 100%;
  }
}



.meter {
  box-sizing: content-box;
  height: 10px;
  position: relative;
  margin: 0;
  background: #ececec;
  padding: 5px;
  box-shadow: inset 0 -1px 1px rgba(255, 255, 255, 0.3);
  width: 100%;
  border: 1px solid #dfdfdf;
}
.meter > span {
  display: block;
  height: 100%;
  border-radius: 2px;
  background-color: #09c;
  background-image: linear-gradient(
    center bottom,
    rgb(43, 194, 83) 37%,
    rgb(84, 240, 84) 69%
  );
  box-shadow: inset 0 2px 9px rgba(255, 255, 255, 0.3),
    inset 0 -2px 6px rgba(0, 0, 0, 0.4);
  position: relative;
  overflow: hidden;
}
.meter > span:after,
.animate > span > span {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-image: linear-gradient(
    -45deg,
    rgba(255, 255, 255, 0.2) 25%,
    transparent 25%,
    transparent 50%,
    rgba(255, 255, 255, 0.2) 50%,
    rgba(255, 255, 255, 0.2) 75%,
    transparent 75%,
    transparent
  );
  z-index: 1;
  background-size: 50px 50px;
  animation: move 2s linear infinite;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  overflow: hidden;
}

.animate > span:after {
  display: none;
}

@keyframes move {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 50px 50px;
  }
}